.product {
   scroll-snap-align: start;
    color: black;
    background-color: white;
    height: 100%;
    display: flex;
    flex-direction: column;

    
}

.pic {
    display: block;
    margin: 0 auto;
  
     
}
.title {
    width: 80%;
    height: 24px;
    margin: -10px 0 5px 10px;
    font-family: "Selavick" sans-serif;
    font-size: 22px;
    font-weight: 700;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.33;
    letter-spacing: normal;
    text-align: left;
    color: black;
    display: flex;
}

.Rectangle1 {
    width: 45px;
    height: 17px;
    margin: 20px 0px 16.5px 10px;
    padding: 2px 5px;
    border-radius: 5px;
    background-color: #ebae21;
  }

.classifier {
    width: 22px;
    height: 8px;
    font-family: "Selavick" sans-serif;
    font-size: 12px;
    font-weight: 600;
    line-height: 1.33;
    text-align: center;
    color: #ffffff;
    
}

.seller {
    margin-top: 30px;
    margin-left: 10px;
    font-family: "Selavick" sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 1.36;
    text-align: left;
    color: black;
  }
  
  .Rectangle4 {
    width: 60%;
    height: 4%;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    padding: 4.5px 87.5px 4px 5.5px;
    border-radius: 10px;
    background-color: #2e2f41;
  }

  .Delivery {
    
    margin: 2px 0 1px 5px;
    font-family: "Selavick" sans-serif;
    font-size: 15px;
    line-height: 1.36;
    letter-spacing: normal;
    text-align: left;
    color: #ffffff;
  }

.icon {
    color: white;
}

.solo {
    background-color: #00cc80;
    width: 50%;
    margin-left: auto;
    margin-right: 0;
    
  }

  .team {
    background-color: #81e3bf;
    width: 50%;
    margin-right: auto;
    margin-left: 0;
    
    
  }

.bar {
  float: left;
  clear: none; 
  display: flex;
  width: 100%;
  margin-top: auto;
  height: 7%;
}
.price {
        
        width: 22px;
        height: 12px;
        margin-left: 35%;
        padding: 2%;
        font-family: "Selavick" sans-serif;
        font-size: 18px;
        font-weight: 600;
        line-height: 1.33;     
        color: #ffffff;
        text-align: center;
    
}

.purchase {
    margin-top: 3%;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 1%;
    font-family: "Selavick" sans-serif;
    font-size: 13px;
    font-weight: 500;
    line-height: 1.33;
    text-align: center;
    color: #ffffff;
  }

.categories {
    overflow-x: scroll; 
    text-align: center;
    position: fixed;
    background-color: white;
    width: 100%;
    display: flex;
    cursor: pointer;
}


.categories h2 {
    display: inline-block;
    color: #4d4d4d;
    text-align: center;
    padding: 10px;
    font-size: 18px;
  }

.categories::-webkit-scrollbar {
    display: none;
}

.categories h2::hover {
  transform: scale(1.09);
}
